/* Item box custom style by dassiorleando */

.box {
    position: relative;
    width: 200px;
    height: 300px;
    font-size: 11px;
    color: #2b2929;
    background-color: transparent;
}
.box-img {
    position: absolute;
    z-index: 999;
    overflow: hidden;
    height: 60%;
    left: 5.2%;
    right:0.1%;
    top: 0.2%;
    margin: 5px;
    background-position: center center;
    background-repeat: no-repeat;
}
.item-img-wrapper, .item-img-wrapper img {
    width: 100%;
    height: 100%;
}
.item-name {
    position: absolute;
    width: 90%;
    top: 61%;
    left: 6.3%;
    white-space: nowrap; 
    z-index: 999;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    padding: 3px;
    font-size: 1.7em;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
}
.item-budget {
    position: absolute;
    z-index: 999;
    width: 80%;
    right: 2%;
    top: 70%;
    padding-right: 5px;
    font-size: 1.7em;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
}
  
.item-budget span:nth-child(1){
    float:left;
}
  
.item-budget span:nth-child(2){
    float: right;
}
.line {
    position: absolute;
    width: 80%;
    top: 80%;
    right: 3%;
    z-index: 999;
    border: .5px solid gray;
    opacity: 0.5;
}
  
.box-info{
    position: absolute;
    display: block;
    z-index: 999;
    font-size:1em;
    opacity: 0.8;
    width: 80%;
    right: 2%;
    top: 82%;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
}
.box-info span:nth-child(1){
    position: absolute;
    top: -7px;
    left: -17%;
    font-size: 30px;
    color: red;
}
.box-info span:nth-child(2){
    float:left;
}
.box-info span:nth-child(3){
    float:right;
}
.box-info span:nth-child(1) svg {
    width: 0.8em;
    height: .8em;
}

.box.arrow-top {
    margin: auto;
    margin-bottom: 20px;
}
  
.box.arrow-top:after {
    content: "";
    position: absolute;
    display: block;
    z-index:0;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: white;
    box-sizing: border-box;
    -webkit-clip-path: polygon( 6% 1%, 99% 1%, 99% 92%, 13% 92%, 1.5% 98%,6.2% 71%);
            clip-path: polygon( 6% 1%, 99% 1%, 99% 92%, 13% 92%, 1.5% 98%,6.2% 71%);
}
  
.box.arrow-top:before {
    content: "";
    position: absolute;
    display: block;
    z-index: 0;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: gray;
    opacity: 0.6;
    box-sizing: border-box;
    -webkit-clip-path: polygon( 5% 0%, 100% 0%, 100% 93%, 13% 93%, 0% 100%, 5% 73%);
            clip-path: polygon( 5% 0%, 100% 0%, 100% 93%, 13% 93%, 0% 100%, 5% 73%);
}

.link-preview-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 14px;
	border-radius: 5px;
	margin: 20px 0px;
	box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1),
	  0 -4px 24px 2px rgba(0, 0, 0, 0.03);
	line-height: 1.5;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
	  "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
	  "Helvetica Neue", sans-serif;
}
.link-preview-section .animated-background, .link-preview-section .link-image-loader .img {
	-webkit-animation-duration: 2.25s;
	        animation-duration: 2.25s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-name: placeHolderShimmer;
	        animation-name: placeHolderShimmer;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
	background: #f6f6f6;
	background: linear-gradient(
		to right,
		#f6f6f6 8%,
		#f0f0f0 18%,
		#f6f6f6 33%
	);
	position: relative;
}
@-webkit-keyframes placeHolderShimmer {
	0% {
		background-position: -468px 0;
	}
	100% {
		background-position: 468px 0;
	}
}
@keyframes placeHolderShimmer {
	0% {
		background-position: -468px 0;
	}
	100% {
		background-position: 468px 0;
	}
}
.link-preview-section .link-description {
	display: flex;
	flex-direction: column;
}
.link-preview-section .link-description .domain {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 4px;
}
.link-preview-section .link-description .domain img {
	height: 16px;
	width: 16px;
}
.link-preview-section .link-description .domain .link-url, .link-preview-section .link-description .domain .link-url-loader {
	font-weight: 600;
}
.link-preview-section .link-description .domain .link-url-loader {
	background-color: #f6f6f6 !important;
	color: #f6f6f6 !important;
	border-radius: 10px;
}
.link-preview-section .link-description .link-data .link-title {
	color: #1364a2;
	font-weight: 600;
	font-size: 12px;
	-webkit-hyphens: auto;
	        hyphens: auto;
	text-align: justify;
}
.link-preview-section .link-description .link-data .link-description {
	font-size: 14px;
	text-align: left;
}
.link-preview-section .link-description .link-data-loader .p1 {
	font-weight: 600;
	font-size: 12px;
}
.link-preview-section .link-description .link-data-loader .p2 {
	font-size: 14px;
}
.link-preview-section .link-description .link-data-loader .p1, .link-preview-section .link-description .link-data-loader .p2 {
	background-color: #f6f6f6 !important;
	color: #f6f6f6 !important;
	border-radius: 10px;
	margin-bottom: 4px;
}
.link-preview-section .link-image {
	display: flex;
	align-content: center;
	align-items: center;
	height: 100%;
}
.link-preview-section .link-image img {
	max-height: 64px;
	object-fit: cover;
	margin-left: 5px;
}
.link-preview-section .link-image-loader {
	display: flex;
	align-content: center;
	align-items: center;
}
.link-preview-section .link-image-loader .img {
	height: 64px;
	width: 64px;
}

