:root {
  --primary: #39A9DB;
  --warning: #F9DC5C;
  --danger: #E84855;
  --success: #ABE188;
  --white: #fdfdfd;
  --dark: #302e2e; }

.btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease; }

.btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px); }

/* Button Colors and Styles */
.btn--primary--solid {
  background-color: #39A9DB;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none; }

.btn--warning--solid {
  border: 4px solid #fdfdfd;
  border: 4px solid var(--white);
  font-style: italic !important;
  box-shadow: -1px 1px 7px 7px #f5f1f1 !important;
  background-color: #302e2e;
  background-color: var(--dark);
  color: #fdfdfd;
  color: var(--white);
  font-weight: 500; }

.btn--warning--solid:hover {
  border: 4px solid #302e2e;
  border: 4px solid var(--dark);
  font-style: italic !important;
  box-shadow: -1px 1px 7px 7px #f5f1f1 !important;
  background-color: #fdfdfd;
  background-color: var(--white);
  color: #302e2e;
  color: var(--dark);
  font-weight: 500; }

.btn--dark--solid {
  background-color: #fdfdfd;
  background-color: var(--white);
  color: #302e2e;
  color: var(--dark);
  border: none; }

.btn--danger--solid {
  background-color: #E84855;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none; }

.btn--success--solid {
  background-color: #ABE188;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none; }

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #39A9DB;
  border: 2px solid var(--primary);
  color: #39A9DB;
  color: var(--primary); }

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #302e2e;
  border: 2px solid var(--dark);
  color: #302e2e;
  color: var(--dark); }

.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #302e2e;
  border: 2px solid var(--dark);
  color: #302e2e;
  color: var(--dark); }

.btn--danger--outline {
  background-color: transparent;
  border: 2px solid #E84855;
  border: 2px solid var(--danger);
  color: #E84855;
  color: var(--danger); }

.btn--success--outline {
  background-color: transparent;
  border: 2px solid #ABE188;
  border: 2px solid var(--success);
  color: #ABE188;
  color: var(--success); }

/* Button Size */
.btn--medium {
  padding: 10px 20px;
  font-size: 18px; }

.btn--large {
  padding: 15px 30px;
  font-size: 20px; }

